import { Component, OnInit } from '@angular/core';
import { Router } from "@angular/router";
import { FormGroup, Validators, FormBuilder, NgForm } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { UserService } from 'src/app/shared/Service/userService/user.service';
import { ToasterService } from 'src/app/shared/Module/toaster/toaster.service';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  formGroup: FormGroup;
  formGroupP: FormGroup;
  currentLanguage="en";
  LanguageImage={
    en:"assets/img/country/us-flag.png",
    ch:"assets/img/country/china-flag.png",
  };
  constructor(
    private userService: UserService,
    private router : Router,
    private _formBuilder: FormBuilder,
    private toastr: ToasterService,
    private translate: TranslateService,
  ) { }

  ngOnInit(): void {
    this.formGroup = this._formBuilder.group({
      username: ['', Validators.required],
      password: ['', Validators.required],
      role: ['agent', Validators.required],
    });
    this.formGroupP = this._formBuilder.group({
      phone: ['',Validators.required],
      role:['']
    });
  }
   //LanguageChagePart
    changeLang(language: string) {
      this.currentLanguage=language;
      localStorage.setItem('language',language)
      this.translate.use(language);
    }
    passwordReset(){
      this.formGroupP.value.role=this.formGroup.value.role;
      this.userService.postRequest("api/resetPassword",this.formGroupP.value,false).subscribe(
        res=>{
          this.Toaster('success','','New password is '+res['result'],4000); 
        },
        err=>{
          this.handleError(err)
        }
      )
    }
   login(){
    const loginData = this.formGroup.value;
    const role= this.formGroup.value.role;
    if(role=="admin") this.loginAdmin(loginData)
    else if(role=="agent") this.loginAgent(loginData)
    else if(role=="user") this.loginUser(loginData)
  }
  loginAdmin(data){
    this.userService.postRequest('api/loginAdmin',data,false).subscribe(
      res=>{
        this.userService.setToken(
          {
            token:res['token'],
            userInfo:res['userInfo'],
            role:data.role,
            expiresAt:res['expiresAt']
          }
        );
        this.Toaster('success','','Welcome'); 
        this.userService.gotoFirstPage()
      },
      err=>{
         this.handleError(err)
      }
    )
  }
  loginAgent(data){
    this.userService.postRequest('api/loginAgent',data,false).subscribe(
      res=>{
        this.userService.setToken(
          {
            token:res['token'],
            userInfo:res['userInfo'],
            role:data.role,
            expiresAt:res['expiresAt']
          }
        );
        this.Toaster('success','','Welcome'); 
        this.userService.gotoFirstPage()
      },
      err=>{
         this.handleError(err)
      }
    )
  }
  loginUser(data){
    this.userService.postRequest('api/loginUser',data,false).subscribe(
      res=>{
        this.userService.setToken(
          {
            token:res['token'],
            userInfo:res['userInfo'],
            role:data.role,
            expiresAt:res['expiresAt']
          }
        );
        this.Toaster('success','','Welcome'); 
        this.userService.gotoFirstPage()
      },
      err=>{
         this.handleError(err)
      }
    )
  }
  //toastr functions
  handleError(err){
    if(err.status==504)  this.Toaster('error', '', 'server is not responsing.',4000);
    else         this.Toaster('error', '', err.error.message,1500);
   }
  Toaster(style,msgtopic,msgContent,time=2000) {
    this.toastr.show(style, msgtopic, msgContent, time);
}
}
